.navbar {
  background-color: #8f5252;
}

.nav-links {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.nav-links li {
  display: inline;
  margin-right: 10px;
}

.nav-links li a {
  color: white;
  text-decoration: none;
  padding: 10px 15px;
}

.nav-links li a:hover {
  background-color: #555;
}
